/* oswald-300 - latin_cyrillic */
@font-face {
     font-family: 'Oswald';
     font-style: normal;
     font-weight: 300;
     src: local(''),
          url('../fonts/oswald-v36-latin_cyrillic-300.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
          url('../fonts/oswald-v36-latin_cyrillic-300.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
   }
